import {
    SIGNUP_SUCCESS,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    SIGNUP_FAIL,
    LOGOUT
} from './types'

import axios from 'axios'
import { setAlert } from './alert'

export const login = (email, password) => async dispatch => {
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({ email, password })

    try {
        const res = await axios.post('http://127.0.0.1:8000/api/token/', body, config)
        dispatch({
            type: LOGIN_SUCCESS,
            payload: res.data
        })

        dispatch(setAlert('Authenticate successfully!!', 'success'))
    }
    catch (err) {
        dispatch({
            type: LOGIN_FAIL
        })

        dispatch(setAlert('Error Authenticating', 'error'))
    }
}


export const signup = ({ name, email, password, password2 }) => async dispatch => {
    const config = {
        headers: {
            'Content-Type': 'application/json'
        }
    }

    const body = JSON.stringify({ name, email, password, password2 })



    try {
        const res = await axios.post(`http://127.0.0.1:8000/api/accounts/signup`, body, config)
        console.log(res.data)
        dispatch({
            type: SIGNUP_SUCCESS,
            payload: res.data

        })

        dispatch(login(email, password))
    }
    catch (err) {
        dispatch({
            type: SIGNUP_FAIL

        })

        dispatch(setAlert('Error Authenticating', 'error'))
    }
}


export const logout = () => dispatch => {
    dispatch(setAlert('logout successfully', 'success'))
    dispatch({ type: LOGOUT })
}