import {
    SIGNUP_SUCCESS,
    LOGIN_SUCCESS,
    LOGIN_FAIL,
    SIGNUP_FAIL,
    LOGOUT
} from '../actions/types'


const initalState = {
    token: localStorage.getItem('token'),
    isAuthenticated: null,
    loading: false
}

export default function (state = initalState, action) {
    const { type, payload } = action

    switch (type) {
        case LOGIN_SUCCESS:
            localStorage.setItem('token', payload.access)
            return {
                ...state,
                isAuthenticated: true,
                loading: false,
                token: payload.access
            }

        case SIGNUP_SUCCESS:
            return {
                ...state,
                isAuthenticated: false,
                loading: true
            }

        case SIGNUP_FAIL:
        case LOGIN_FAIL:
        case LOGOUT:
            localStorage.removeItem('token')
            return {
                ...state,
                token: null,
                isAuthenticated: false,
                loading: false
            }

        default:
            return state
    }
}