import React, { useEffect, useState, Fragment } from 'react';
import { Helmet } from 'react-helmet'
import axios from 'axios'
import House from '../assests/images/home3.jpg'

const About = () => {
    const [topSeller, setTopSeller] = useState([])
    const [realtors, setRealtors] = useState([])

    useEffect(() => {
        axios.defaults.headers = {
            'Content-Type': 'application/json'
        }

        const getTopSellers = async () => {
            try {
                const res = await axios.get('http://127.0.0.1:8000/api/realtors/topseller');
                setTopSeller(res.data)

            }
            catch (err) {

            }
        };

        getTopSellers()

    }, []);

    useEffect(() => {
        axios.defaults.headers = {
            'Content-Type': 'application/json'
        }

        const getRealtors = async () => {
            try {
                const res = await axios.get('http://127.0.0.1:8000/api/realtors/');
                setRealtors(res.data)


            }
            catch (err) {

            }
        };

        getRealtors()

    }, []);

    const getAllRealtors = () => {
        let allRealtors = []
        let results = []

        realtors.map(realtor => {
            return allRealtors.push(
                <Fragment key={realtor.id}>
                    <div className="about__display">
                        <img src={realtor.photo} className="about__display__image" alt='' />
                    </div>
                    <h3 className="about__realtor">{realtor.name}</h3>
                    <p className="about__contact">{realtor.phone}</p>
                    <p className="about__realtor">{realtor.email}</p>
                    <p className="about__about">{realtor.description}</p>
                </Fragment>
            )
        })

        for (let i = 0; i < realtors.length; i += 3) {
            results.push(
                <div className="row" key={i}>
                    <div className="col-1-of-3">
                        {allRealtors[i]}
                    </div>
                    <div className="col-1-of-3">
                        {allRealtors[i + 1] ? allRealtors[i + 1] : null}
                    </div>
                    <div className="col-1-of-3">
                        {allRealtors[i + 2] ? allRealtors[i + 2] : null}
                    </div>
                </div>
            )
        }
        return results;
    }

    const getTopSeller = () => {
        let result = []


        topSeller.map(seller => {
            return result.push(
                <Fragment key={seller.id}>
                    <div className="about__display">
                        <img src={seller.photo} className="about__display__image" alt='' />
                    </div>
                    <h3 className="about__topseller">Top Seller: </h3>
                    <p className="about__realtor">{seller.name}</p>
                    <p className="about__contact">{seller.phone}</p>
                    <p className="about__contact">{seller.email}</p>
                    <p className="about__about">{seller.description}</p>
                </Fragment>
            )
        })

        return result;
    }

    return (
        <main className="about">
            <Helmet>
                <title>Realest Estate - About</title>
                <meta content="About Page" name="description" />
            </Helmet>
            <header className="about__header">
                <h1 className="about__heading">About Realest Estate</h1>
            </header>
            <section className="about__info">
                <div className="row">
                    <div className="col-3-of-4">
                        <h2 className="about__subheading">We find the Perfect Home for you</h2>
                        <p className="about__paragraph">
                            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Donec placerat suscipit nisi sit amet feugiat.
                            Vestibulum maximus odio eu tellus pulvinar, vitae bibendum tortor facilisis. Vestibulum luctus urna non
                            urna suscipit vestibulum. Nulla et massa ut ipsum facilisis egestas.
                            Vestibulum nec ligula at dolor elementum tincidunt vitae aliquet velit. Fusce dui sapien, pulvinar vel
                            felis at, varius semper risus. Curabitur pellentesque ex vel metus pharetra venenatis. In ut lobortis
                            nisi, vitae tincidunt urna. Fusce dignissim finibus commodo.
                        </p>
                        <div className="about__display">
                            <img className="about__display__image" src={House} alt='' />
                        </div>
                        <p className="about__paragraph">
                            Donec dignissim lectus id sapien consequat mattis. Donec suscipit dui non justo aliquet, a mattis
                            lectus fermentum. Sed sagittis iaculis libero a ornare. Donec id neque ut dui vestibulum finibus et
                            id felis. Morbi consequat enim at mauris aliquet, non tristique elit viverra. Duis sit amet nulla
                            porttitor, gravida elit vel, consectetur urna. In vehicula auctor ultrices. Donec rutrum nisi ac mi
                            auctor volutpat. Sed consequat massa turpis, eget ornare nisl euismod in. Duis porttitor dolor elit,
                            a rutrum sapien mattis porta. Sed id faucibus urna. Donec lobortis vel quam vel interdum. Praesent rhoncus,
                            orci vel ullamcorper tempor, magna mi tristique enim, et vulputate ipsum metus a dolor.
                        </p>
                    </div>
                    <div className="col-1-of-4">
                        {getTopSeller()}
                    </div>
                </div>
            </section>

            <section className="about__team">
                <div className="row">
                    <h2 className="about__subheading">Meet out Awesome Team!!</h2>
                </div>
                {getAllRealtors()}
            </section>
        </main>
    )
}

export default About;
